
#stull-chart-d3 {
  min-height: 400px;
  min-width: 300px;
}
.form-inline .form-group {
  margin-right: .175em;
  margin-bottom: .25em;
}
.orton-cone-colors tr td {
  font-size: .85em;
  padding: .2em;
  border: 1px solid white;
}
.r2o-colors tr td {
  color: #ffffff;
  font-size: .85em;
  line-height: 1em;
  padding: .2em;
  width: 2.5em;
  margin: 1px;
  text-align: center;
}
.r2o-colors tr td.label {
  color: #000000;
  white-space:nowrap;
  width: 60px;
}
.r2ocolor-b-100 { background-color: #FF0000;
}
